import React from 'react'
import styled from 'styled-components'
import fonepay from './fonepay.jpg'
import e_sewa from '../../images/esewa-logo.png'

const Qrcode = styled.div`
    margin-top: 50px;
    text-align:center;
    margin-bottom: 120px;
.e_sewa{
    text-align:center;
    margin-bottom:100px;
    
}
.e_sewa img{
    width:100px;
    height:auto;
}
.fonepay{
    width: 500px;
    height:auto;
}
@media screen and (max-width:500px){
.fonepay{
    width: 350px;
    height:auto;
}
}
`

const Payment = () => {
    return (
       <> 
        <Qrcode>
          <div className='e_sewa'>
           <img src={e_sewa} alt="e_sewa" /><br />
           <b>eSewa id : 9876543210</b>
          </div>
          <img className='fonepay' src={fonepay} alt="fonepay" />
        </Qrcode>
       </> 
    )
}

export default Payment
